import React, { useEffect, useState } from "react";
import "../statics/css/Home.css";
import Typewriter from "typewriter-effect";
import { Row, Col, Navbar, Nav, Image, Table, ProgressBar, Form, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { HashLink as Hlink } from "react-router-hash-link";
import Pic from "../statics/media/picture.png";
import Facebook from "../statics/media/facebook.svg";
import Instagram from "../statics/media/instagram.svg";
import Linkedin from "../statics/media/linkedin.svg";
import Github from "../statics/media/github.svg";

function Typer() {

    return (
        <Typewriter

            onInit={(typewriter) => {

                typewriter

                    .typeString("Clitus")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("A Full Stack Developer")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("Clitus")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("A Full Stack Developer")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("Clitus")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("A Full Stack Developer")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("Clitus")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("A Full Stack Developer")
                    .pauseFor(2000)
                    .deleteAll()
                    .typeString("Clitus")
                    .start();
            }}
        />
    );
}

function Home() {

    const [email, changeEmail] = useState("");
    const [field, changeField] = useState("");
    const [fieldState, changeFieldState] = useState(true);
    const [buttonState, changeButtonState] = useState(true);

    useEffect(
        () => {
            if (email !== "") {
                changeFieldState(false)
                if (field !== "") {
                    changeButtonState(false);
                }
                else {
                    changeButtonState(true);
                }
            }

            else {
                changeFieldState(true);
                changeButtonState(true);
            }
        }, [email, field]
    );

    return (
        <>
            <Navbar className="navbar" variant="dark" expand="md">
                <Navbar.Brand className="brand">Clitus</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse className="collapse" id="basic-navbar-nav">
                    <Nav className="nav">
                        <Nav.Link className="navlink">
                            <Hlink className="link" to="#about">About</Hlink>
                            <div className="hoverdiv"><div className="hoverline"></div></div>
                        </Nav.Link>
                        <Nav.Link className="navlink">
                            <Hlink className="link" to="#projects">Projects</Hlink>
                            <div className="hoverdiv"><div className="hoverline"></div></div>
                        </Nav.Link>
                        <Nav.Link className="navlink">
                            <Hlink className="link" to="#skills">Skills</Hlink>
                            <div className="hoverdiv"><div className="hoverline"></div></div>
                        </Nav.Link>
                        <Nav.Link className="navlink">
                            <Hlink className="link" to="/resume">Resume</Hlink>
                            <div className="hoverdiv"><div className="hoverline"></div></div>
                        </Nav.Link>
                        <Nav.Link className="navlink">
                            <Hlink className="link" to="#contact">Contact</Hlink>
                            <div className="hoverdiv"><div className="hoverline"></div></div>
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
            <Row as="div" id="description">
                <Col as="div" className="welcome" md={5}>
                    <h1>Hii<span>!!</span> I'm</h1>
                    <div className="typer">
                        <h2><Typer /></h2>
                    </div>
                    <div className="contactdiv">
                        <Hlink className="contact" to="#contact">Contact Me</Hlink>
                    </div>
                </Col>
                <Col as="div" className="image" md={7}>
                    <Image fluid src={Pic} height="200px" />
                </Col>
            </Row>
            <Row as="div" id="about">
                <Col as="div" className="about" md={12}>
                    <h1>About Me</h1>
                    <div>
                        <text>
                            I am a college student who is well interested in web development. I love React js and my Passion is to become a full stack developer. I know React js, Node js, Jquery e.t.c. Python, Java, C# are some of the programming languages known by me.
                        </text>
                    </div>
                </Col>
            </Row>
            <Row as="div" id="projectDescription">
                <Col className="info" md={12}>
                    <h1>My Projects</h1>
                    <div>
                        <text>
                            I have made these projects using MERN stack. Please, check these projects to know my skill in web development. I provided the live preview of the First project deployed in github pages and remaining two projects were not deployed yet. You can find their source code is in my Github.
                        </text>
                    </div>
                </Col>
            </Row>
            <Row as="div" id="projects">
                <Col className="project" md={4}>
                    <h1>College Clone website</h1>
                    <div>
                        <text> I have made a clone of my college website with exact details. It will be quite better than original website. Because, It is made up of React and Node as it is Single page website.</text>
                    </div>
                    <Link className="link" to="https://clituschris.github.io/MTCET/">Live Preview</Link>
                </Col>
                <Col className="project" md={4}>
                    <h1>Library Management System</h1>
                    <div>
                        <text> I have made a Library Management System For my College using Node Js and React Js. I have connected it with mySql database for backend. I have done this as my mini project in college.</text>
                    </div>
                </Col>
                <Col className="project" md={4}>
                    <h1>Fertilizer Recommendation System</h1>
                    <div>
                        <text> Me and my four college friends have made a Fertilizer Recommendation System to detect plant disease using Flask. this System has a Trained model to detect the disease from the image of leaves of plants.</text>
                    </div>
                </Col>
            </Row>
            <Row as="div" id="skills">
                <Col md={1}></Col>
                <Col as="div" className="skills" md={3}>
                    <h1>My Skills</h1>
                    <div>
                        <text>I have some Skills in web developing and its corresponding tools and frameworks. I have mentioned some of my Skills below.</text>
                    </div>
                </Col>
                <Col as="div" md={7} className="table">
                    <Table >
                        <tbody>
                            <tr>
                                <td>html,css,Js</td>
                                <td>Expert</td>
                                <td><ProgressBar now={95} variant="success" label="95%" /></td>
                            </tr>
                            <tr>
                                <td>C, C++, C#</td>
                                <td>Basic</td>
                                <td><ProgressBar now={20} variant="success" label="20%" /></td>
                            </tr>
                            <tr>
                                <td>Java</td>
                                <td>Intermediate</td>
                                <td><ProgressBar now={60} variant="success" label="60%" /></td>
                            </tr>
                            <tr>
                                <td>Python</td>
                                <td>expert</td>
                                <td><ProgressBar now={80} variant="success" label="80%" /></td>
                            </tr>
                            <tr>
                                <td>Node Js</td>
                                <td>Intermediate</td>
                                <td><ProgressBar now={50} variant="success" label="50%" /></td>
                            </tr>
                            <tr>
                                <td>React Js</td>
                                <td>Intermediate</td>
                                <td><ProgressBar now={50} variant="success" label="50%" /></td>
                            </tr>
                            <tr>
                                <td>Express Js</td>
                                <td>Basic</td>
                                <td><ProgressBar now={30} variant="success" label="30%" /></td>
                            </tr>
                            <tr>
                                <td>mySql, Mongodb</td>
                                <td>Intermediate</td>
                                <td><ProgressBar now={55} variant="success" label="55%" /></td>
                            </tr>
                        </tbody>
                    </Table>
                </Col>
                <Col md={1}></Col>
            </Row>
            <Row id="contact">
                <Col className="contact" md={12}>
                    <h2>Ask Anything ?</h2>
                    <Form>
                        <div className="inputdiv">
                            <input name="email" placeholder="Please Enter your email" type="email" onChange={(e) => { changeEmail(e.target.value) }} />
                        </div>
                        <div className="inputdiv">
                            <textarea type="textbox" name="comment" placeholder="Ask Me" onChange={(e) => { changeField(e.target.value) }} disabled={fieldState} />
                        </div>
                        <div className="inputdiv">
                            <Button type="reset" variant="success" disabled={buttonState}>Submit</Button>
                        </div>
                    </Form>
                </Col>
            </Row>
            <Row as="div" className="footer">
                Follow Me on:
                <div className="icons">
                    <Image className="icon" src={Facebook} />
                    <Image className="icon" src={Instagram} />
                    <Image className="icon" src={Linkedin} />
                    <Image className="icon" src={Github} />
                </div>
            </Row>
        </>
    );
}

export default Home;