import React, { useState } from "react";
import "../statics/css/Resume.css";
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import ResumeFile from "../statics/media/Resume.pdf"
import { Button, Image } from "react-bootstrap";
import Download from "../statics/media/download.svg";

function Resume() {

    const [numPages, changeNumPages] = useState(null);
    const [pageNum, changePageNum] = useState(1);

    function onDocumentLoadSuccess({ numPages }) {
        changeNumPages(numPages);
        changePageNum(1);
    }

    return (
        <div className="pdfViewer">
            <div>
                <div className="button">
                    <a href={ResumeFile} download={ResumeFile}>
                        <Button variant="danger"><Image className="icon" src={Download}></Image><span>Download</span></Button>
                    </a>
                </div>
                <Document file={ResumeFile} onLoadSuccess={onDocumentLoadSuccess}>
                    <Page height="800" className="page" pageNumber={pageNum}>
                    </Page>
                </Document>
            </div>
        </div>
    );
}

export default Resume;